import Link from 'next/link';
import Image from 'next/image';
import logoCeea from '../../public/logoceea.png';
import styles from '../ui/styles.module.css';
import ilustrationSchool1 from '../../public/wpp-logo.png';
import ilustrationSchool2 from '../../public/school-ilustration2.png';

export default function Page() {
  return (
    <main className="flex min-h-screen flex-col bg-yellow-500">
      <div className="flex h-[60px] shrink-0 justify-between items-center bg-yellow-500 p-4 md:h-[60px]">
        {/* azul: #010066 amarelo: #f5e600 */}
        <Image width={60}  src={logoCeea} alt="logo-ceea"/>
        <div className="flex justify-center items-center">
          <Link href="/" className="flex items-center gap-5 self-start rounded-lg  px-6 py-3 text-sm font-medium text-white ">
            <span >
              HOME
            </span>
          </Link>
          <Link href="/contato" className="flex items-center gap-5 self-start rounded-lg  px-6 py-3 text-sm font-medium text-white ">
            <span >
                CONTATO
            </span>
          </Link>
        </div>
      </div>

      <div className={styles.background2}>
        <div className='container mx-auto'>
          <div className='flex flex-wrap'>
            
            <div className="w-full md:w-8/12 mt-20 px-10">
              <h1 className='text-lg font-bold text-white text-center mt-10'>WHATSAPP</h1>
              <Image className='mx-auto mt-10' width="35" src={ilustrationSchool1} alt="wpp-logo"/>
              <h1 className='text-center text-white font-bold mt-5'> (75) 99864-9507</h1>
            </div>
            <div className="w-full md:w-4/12 flex mt-20">
              <Image src={ilustrationSchool2} alt="illustration school" />
            </div>
          </div>
          <div className='flex flex-wrap justify-center'>
            

          </div>
        </div>
      </div>

      <div className={styles.footer}>
        <h1 className='text-center text-white font-bold mt-5'>
          feito por: Barros Software
        </h1>
      </div>
    </main>
  );
}